.App {
  text-align: center;
  background-color: beige;
  height: 100%;
  /* color: white; */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 8vw;
}
.row {
  display: flex;
  flex-direction: row;
}
.col-50 {
  width: 50vw;
    display: flex;
    flex-direction: column;
}
.row-50 {
  /* height: 50%; */
}
/* NAME */
.name {
  display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-image: url("./images/BlankRibbon_trans.png");
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center; */
    height: 16vw;
    padding-top: 4rem;
    padding-bottom: 4rem;
}
.name-text {
  font-size: 17vw;
  font-weight: bolder;
  color: rgb(158, 65, 156);
}
.name-input {
  width: 80vw;
  height: 10vw;
  /* margin-top: 50px; */
  font-size: 10vw;
}
/* GRADE */
.grade {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 26vw;
  /* font-size: 15rem; */
  /* padding-top: 4rem;
  padding-bottom: 4rem; */
}

.grade-text {
  font-size: 18vw;
  font-weight: bolder;
  margin-top: -5vw;
}
.grade-dropdown {
  font-size: 5vw;

}

.age-text-wrapper {
  /* background-image: url("./images/apple.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center; */
}

/* AGE */
.age {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 35vw;
  background-image: url("./images/apple.png");
    background-repeat: no-repeat;
    /* background-size: contain; */
    background-size: 20vw;
    background-position: center;
    margin-bottom: 5rem;
}

.age-text {
  font-size: 15vw;
    font-weight: bold;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    margin-bottom: -2vw;
}
.age-input_wrapper{

  display: flex;
    align-items: center;
    justify-content: center;
}
.age-input{
  height: 16vw;
  width: 16vw;
  font-size: 10vw;
  
}
.favorite {
  margin-top: 5rem;;
}
.fav-ul{
  margin-top: 0;
  margin-bottom: 0;
}
.fav-li {
  margin-bottom: -2vw;
}
.fav-input_wrapper{
  display: flex;
  flex-direction: column;
  padding-left: 3vw;
  padding-right: 1vw;

}
.fav-input{
  font-size: 5vw;
    height: 6.75vw;
    margin: 0.5vw;
}
.fav-list {
  font-size: 6vw;
  /* border: black 1vw solid;
  border-radius: 10%;
  width: 35vw; */
  background-image: url("./images/file.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

  
}
.multicolor-text {
  background: linear-gradient(to left,
          violet,
          indigo,
          blue,
          green,
          yellow,
          orange,
          red);
  -webkit-background-clip: text;
  color: transparent;
}



